input[type=submit] {
    text-transform: uppercase;
    background: #fb8500;
    margin: 1em 0 0;
    color: white;
    border: none;
    border-radius: 8px;
    transition: all .3s ease-out;
}

input:focus,
input:hover,
select:focus,
select:hover,
textarea:focus,
textarea:hover {
    background: lavenderBlush;
}

input[type=submit]:hover,
input[type=submit]:focus {
    background: #e85d04;
    text-transform: uppercase;
    outline: none;
}

